import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Modal from 'react-modal';
import Timer from 'react-compound-timer';
import Cookies from 'universal-cookie';

const cookies = new Cookies();

class App extends Component {

    constructor(props) {
        super(props);
        this.state = {
            delta: this.getCookie('delta') || 22,
            boshift: this.getCookie('boshift') || .5,
            bo: 0,
            max: 0,
            instructionsOpen: true,
            configurationOpen: false,
            progressOpen: false,
            distance: this.getCookie('distance') || 40,
            height: this.getCookie('height') || 500,
            smallLength: this.getCookie('smallLength') || 100,
            shift: this.getCookie('shift') || 16,
            time: this.getCookie('time') || 10,
            redLeft: this.getCookie('redLeft') || false
        };
        this.windowWidth = this.getCookie('windowWidth') || 30;
        this.distance = this.getCookie('distance') || 40;
        this.ppcm = this.getCookie('ppcm') || 56;
        this.side = 0;
        this.before = false;
        this.red = Number(this.state.redLeft * 2)
        this.blue = Number(!this.state.redLeft * 2)
        console.log(this.red);
        console.log(this.blue);
        this.ac = new AudioContext();
        this.timer = React.createRef();
        this.progress = cookies.get('progress') || "{}";
    }

    getCookie(name) {
        return Number(cookies.get(name));
    }

    persistCookies() {
        cookies.set('delta', this.state.delta);
        cookies.set('boshift', this.state.boshift);
        cookies.set('distance', this.state.distance);
        cookies.set('height', this.state.height);
        cookies.set('smallLength', this.state.smallLength);
        cookies.set('shift', this.state.shift);
        cookies.set('time', this.state.time);
        cookies.set('redLeft', this.state.redLeft);
        cookies.set('windowWidth', this.windowWidth);
        cookies.set('distance', this.distance);
        cookies.set('ppcm', this.ppcm);
    }

    componentDidMount() {
        document.title="Convergence Insufficiency Trainer";
        document.addEventListener("keydown", this._handleKeyDown);
        this.populateCanvas();
    }

    _handleKeyDown = (e) => {
        switch(e.keyCode) {
            case 32:
                this.setState((prevState, props) => {
                    return { bo: prevState.bo - this.state.boshift };
                });
                this.beep(100, 220, 200);
                this.populateCanvas();
                break;
            case 37:
            case 38:
            case 39:
            case 40:
                this.timer.current.start();
                if (this.side == e.keyCode) {
                    this.beep(100, 520, 200);
                    if (this.before) {
                        this.before = false;
                        this.setState((prevState, props) => {
                            return { bo: prevState.bo + this.state.boshift,
                                     max: Math.max(prevState.bo + this.state.boshift, prevState.max)
                            };
                        });
                    } else {
                        this.before = true;
                    }
                } else {
                    this.beep(100, 220, 200);
                    if (this.before) {
                        this.before = false;
                    } else {
                        this.before = true;
                        this.setState((prevState, props) => {
                            return { bo: prevState.bo - this.state.boshift };
                        });
                    }
                }
                this.populateCanvas();
                break;
            case 67:
                this.setState({configurationOpen: true});
                break;
            case 73:
                this.setState({instructionsOpen: true});
                break;
            case 80:
                this.setState({progressOpen: true});
            default:
                break;
        }
    }

    beep(vol, freq, duration){
        var v=this.ac.createOscillator();
        var u=this.ac.createGain();
        v.connect(u);
        v.frequency.value=freq;
        v.type="square";
        u.connect(this.ac.destination);
        u.gain.value=vol*0.01;
        v.start(this.ac.currentTime);
        v.stop(this.ac.currentTime+duration*0.001);
    }

    populateCanvas() {
        if (this.state.bo < 0) {
            this.setState((prevState, props) => {
                return { bo: 0 };
            });
        }

        const canvas = this.refs.canvas;
        const ctx = canvas.getContext("2d");

        var offset = Math.round(this.state.bo * this.state.delta);
        var width = this.state.height + offset;

        canvas.width = width;
        canvas.height = this.state.height;
        var stereogram = ctx.createImageData(width, this.state.height);

        this.side = Math.floor(Math.random() * 4) + 37;

        var pad = this.state.height / 10
        var center = this.state.height / 2 - this.state.smallLength / 2

        switch(this.side) {
            case 37:
                // left
                this.buildStereograms(stereogram.data, width, offset, pad, center);
                break;
            case 38:
                // up
                this.buildStereograms(stereogram.data, width, offset, center, pad);
                break;
            case 39:
                // right
                this.buildStereograms(stereogram.data, width, offset, this.state.height - this.state.smallLength - pad, center);
                break;
            case 40:
                // down
                this.buildStereograms(stereogram.data, width, offset, center, this.state.height - this.state.smallLength - pad);
        }

        ctx.putImageData(stereogram, 0, 0);
    }

    buildStereograms(imageData, width, bo, smallX, smallY) {
        console.log(this.blue);
        console.log(this.red);
        for (var i = 0; i < imageData.length; i+=4) {
            var x = i/4 % width;
            var y = Math.floor((i/4) / width);

            var center = (width - bo) / 2;

            if (x < (width - bo)) {
                if ((center - 7) < x && x < (center + 7) && (center - 7) < y && y < (center + 7)) {
                    imageData[i + this.red] = 255;
                } else if ((center - 10) < x && x < (center + 10) && (center - 10) < y && y < (center + 10)) {
                    imageData[i + this.red] = 0;
                } else {
                    imageData[i + this.red] = Math.round(Math.random()) * 255;
                }
            } else {
                imageData[i + this.red] = 0;
            }

            if (x < bo) {
                imageData[i + this.blue] = 0;
            } else {
                if ((x > smallX + bo) && (x < smallX + this.state.smallLength + bo)
                    && (y > smallY) && (y < smallY + this.state.smallLength)) {
                    imageData[i + this.blue] = imageData[i + this.red - bo * 4 - 4 * this.state.shift];
                } else {
                    imageData[i + this.blue] = imageData[i + this.red - bo * 4];
                }
            }
            imageData[i + 1] = 0;
            imageData[i + 3] = 255;
        }
    }

    handleInputChange = (event) => {
        this.setState({
            [event.target.name]: Number(event.target.value)
        });
        this.populateCanvas();
    }

    updateTime = (event) => {
        this.setState({
            [event.target.name]: Number(event.target.value)
        });
        this.timer.current.setTime(Number(event.target.value) * 60000);
        this.timer.current.start();
        this.timer.current.stop();
        this.populateCanvas();
    }

    updateWindowWidth = (event) => {
        this.windowWidth = Number(event.target.value);
        this.ppcm = Math.max(
            document.body.scrollWidth,
            document.documentElement.scrollWidth,
            document.body.offsetWidth,
            document.documentElement.offsetWidth,
            document.documentElement.clientWidth
        ) / this.windowWidth;
        console.log(this.ppcm * this.distance / 100);
        this.setState({ delta: Math.round(this.ppcm * this.distance / 100)});
    }

    updateDistance = (event) => {
        this.distance = Number(event.target.value);
        this.setState({ delta: Math.round(this.ppcm * this.distance / 100)});
    }

    handleCheck = (event) => {
        var temp = this.red;
        this.red = this.blue;
        this.blue = temp;
        this.setState((prevState, props) => {
            return { redLeft: !prevState.redLeft};
        });
        this.populateCanvas();
    }

    render() {
        return (
            <div className="App">
                <canvas ref="canvas" width="1000" height="500"></canvas>
                <br />
                <div>
                    <font color="white">BO {this.state.bo} MAX {this.state.max} TIME
                        <Timer
                            ref={this.timer}
                            initialTime={this.state.time * 60000}
                            direction="backward"
                            startImmediately={false}
                            checkpoints={[{
                                    time: 0,
                                    callback: () => {
                                        this.beep(100, 300, 1000);
                                        var today=new Date().toISOString().slice(0, 10);
                                        if (!(today in this.progress) || this.state.max > this.progress[today]) {
                                            this.progress[today] = this.state.max;
                                            cookies.set('progress', JSON.stringify(this.progress));
                                        }
                                    }
                            }]}
                        >
                            {() => (
                                <React.Fragment>
                                    <Timer.Minutes /> m <Timer.Seconds /> s
                                </React.Fragment>
                            )}
                        </Timer><br />
                        <i>[i]nstructions, [c]onfiguration, [p]rogress</i>
                    </font>
                </div>
                <Modal
                    isOpen={this.state.instructionsOpen}
                    onRequestClose={() => {
                            this.setState({instructionsOpen: false});
                    }}
                    contentLabel="Instructions"
                >
                    <h2>INSTRUCTIONS</h2><br />
                    <p>This webpage contains an interactive training tool for addressing convergence insuffiency as an alternative to pencil pushups. It utilizes random dot stereograms of varying vergence demands. Please note that this tool was developed by a layperson for personal use and does not constitute medical advice. Talk to your opthamologist!</p>
                    <p>Before using this tool, it is important to set certain configuration options. Using a ruler, measure the physical width of the webpage in centimeters. I suggest that you sit 40cm/16in from the screen, but if you plan on sitting a different distance, record that as well. By default, a practice session will last ten minutes, but you can adjust the timer to a different time if you so choose (or your doctor recommends). By default, we assume that the red lens covers your right eye. If that is not true, or if you want to train divergence, check the box indicating that your left lens is red. All other defaults are probably sane. Press [c] to pull up the configuration modal. Settings are stored via a cookie so next time you visit the page you won't need to re-enter them.</p>
                    <p>To use, wear red-blue 3d glasses and stare at the colored square in the middle of the screen. Along one of the sides there will be a floating square. Press the arrow key that corresponds to the side of the large square the floating image is on (e.g. if it's on the top, press the up arrow key). The page will emit a different tone depending on if you are correct or not. As you correctly identify the floating square, it will slowly move its red and blue subcomponents further apart, increasing the effort your eyes need to make in order to fuse the images together. If, after a few seconds, you cannot fuse the images or find the floating square, hit the spacebar to bring them closer together.</p>
                    <p>Over time, you should be able to converge images that are farther apart. At the end of each session, your max score is stored. Press [p] to view your max score across different dates.</p>
                    <p>To close the instructions (or other modals) click on the page outside of the box.</p>
                </Modal>
                <Modal
                    isOpen={this.state.progressOpen}
                    onRequestClose={() => {
                            this.setState({progressOpen: false});
                    }}
                    contentLabel="Progress"
                >
                    <h2>PROGRESS</h2><br />
                    {JSON.stringify(this.progress)}
                </Modal>
                <Modal
                    isOpen={this.state.configurationOpen}
                    onRequestClose={() => {
                            this.persistCookies();
                            this.setState({configurationOpen: false});
                    }}
                    contentLabel="Configuration"
                >
                    <h2>CONFIGURATION</h2><br />
                    <form>
                        <label>
                            Width of window (cm):
                            <input
                                name="windowWidth"
                                type="number"
                                defaultValue={this.windowWidth}
                                onBlur={this.updateWindowWidth} />
                        </label><br />
                        <label>
                            Distance to screen (cm):
                            <input
                                name="distance"
                                type="number"
                                defaultValue={this.distance}
                                onBlur={this.updateDistance} />
                        </label><br />
                        <label>
                            Height of square (px):
                            <input
                                name="height"
                                type="number"
                                defaultValue={this.state.height}
                                onBlur={this.handleInputChange} />
                        </label><br />
                        <label>
                            Height of offset square (px):
                            <input
                                name="smallLength"
                                type="number"
                                defaultValue={this.state.smallLength}
                                onBlur={this.handleInputChange} />
                        </label><br />
                        <label>
                            Shift of offset square (px):
                            <input
                                name="shift"
                                type="number"
                                defaultValue={this.state.shift}
                                onBlur={this.handleInputChange} />
                        </label><br />
                        <label>
                            Delta of prism diopter demand:
                            <input
                                name="boshift"
                                type="boshift"
                                defaultValue={this.state.boshift}
                                onBlur={this.handleInputChange} />
                        </label><br />
                        <label>
                            Time per session (minutes):
                            <input
                                name="time"
                                type="number"
                                defaultValue={this.state.time}
                                onBlur={this.updateTime} />
                        </label><br />
                        <label>
                            Check if red lens is over left eye:
                            <input
                                name="redLeft"
                                type="checkbox"
                                checked={this.state.redLeft}
                                onClick={this.handleCheck} />
                        </label>
                    </form>
                </Modal>
            </div>
        );
    }
}

export default App;
